import * as THREE from 'three';
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader.js';

const PUBLIC_URL = `${process.env.PUBLIC_URL}/assets`;
export class Environment{
    
    private loader:FBXLoader;

    constructor(){
        this.loader = new FBXLoader();
    }

    getRandomInt(max:number) {
        return 10 + Math.floor(Math.random() * (max-10));
    }

    load(scene: THREE.Scene){
        const scope=this;
        const _OnLoad = (name, fbx:THREE.Object3D) => {
            fbx.scale.setScalar(0.1);
            fbx.position.x = 50-scope.getRandomInt(100);
            fbx.position.z = 50-scope.getRandomInt(100);
            fbx.traverse(c => {
              c.castShadow = true;
            });
            scene.add(fbx);
          };

        const elements = [
 
            
            'Bush_1.fbx', 
            'Bush_2.fbx', 
            // 'Tree_1.fbx', 
            // 'Tree_2.fbx', 
            // 'Tree_3.fbx',
            'Log_1.fbx',
            'Log_2.fbx',
            'Log_3.fbx',

            'Plant_1.fbx',
            'Plant_2.fbx',
            'Plant_3.fbx',
            'Plant_4.fbx',
            'Plant_5.fbx',
            'Plant_6.fbx',
            'Plant_7.fbx',

        ];
        // const elements = ['EA_Puddle_01a', 'EA_Fruit_01a', 'EA_Clif_01a', 'EA_Clif_01b', 'EA_Clif_01c', 'EA_Clif_01d'];
        

        
        // scope.loader.load(`${PUBLIC_URL}/models/environment/environment1/portal.fbx`, (fbx) => {
        //     fbx.scale.setScalar(.001);
        //     scene.add(fbx);
        // });

        elements.forEach((el)=>{
            scope.loader.load(`${PUBLIC_URL}/models/environment/environment1/${el}`, (model) => { _OnLoad('walk', model); });
        });
        
    }
}